export default function AddTerm(props) {
  return (
    <div>
      <button onClick={() => console.log("was clicked")}>add term</button>
    </div>
  );
}

function AfterClick() {
  return (
    <Query query={fetchTweetsQuery}>
      {({ loading, error, data }) => {
        if (error) return <div>no data loaded</div>;
        if (loading) return <div>Loading</div>;

        return (
          <div>
            <div className="dash-main-table">
              <table>
                <thread>
                  <tr>
                    <th>query: {this.props.initialTerm}</th>
                  </tr>
                </thread>
                <tbody>
                  <div>
                    {data.fetchTweets.map(tweet => (
                      <tr>
                        <th scope="row">text</th>
                        <td>{tweet.text}</td>
                      </tr>
                    ))}
                  </div>
                </tbody>
              </table>
              <style jsx>
                {`
                  .dash-main-table {
                    max-width: 50%;
                    margin: 3em auto 3em auto;
                  }
                `}
              </style>
            </div>
          </div>
        );
      }}
    </Query>
  );
}

export const fetchTweetsQuery = gql`
  query fetchTerms {
    fetchTerms {
      id
      term
    }
  }
`;
